/*------------------------------------*\
#BUTTONS
\*------------------------------------*/
.btn {
	font-family: Montserrat, sans-serif;
	position: relative;
	z-index: 2;
	font-size: 16px;
	font-weight: 600;
	text-transform: uppercase;
	text-align: center;
	border-radius: 0;
	padding: 0 30px;
	line-height: 45px;
}

.btn.active,
.btn:active {
	-webkit-box-shadow: none;
	box-shadow: none;
}

/* Button Primary */
.btn--primary {
	background-color: #5ca466;
	color: #ffffff;
	border: none;
}

.btn--primary:active,
.btn--primary:focus,
.btn--primary:hover {
	background-color: #37b05d;
	color: #ffffff;
}

.bg-dark .btn--primary:active,
.bg-dark .btn--primary:focus,
.bg-dark .btn--primary:hover {
	background-color: #37b05d;
	color: #ffffff;
}

/* Button Secondary*/
.btn--secondary {
	background-color: #333333;
	color: #ffffff;
	border: 2px solid #333333;
}

.btn--secondary:active,
.btn--secondary:focus,
.btn--secondary:hover {
	background-color: #5ca466;
	color: #ffffff;
	border-color: #5ca466;
}

/* Button White */
.btn--white {
	background-color: #ffffff;
	color: #333333;
	border: 1px solid #ffffff;
	border-radius: 4px;
}

.btn--white:active,
.btn--white:focus,
.btn--white:hover {
	background-color: #5ca466;
	color: #ffffff;
	border-color: #5ca466;
}

/* Button Block */
.btn--block {
	width: 100%;
}

.btn--auto {
	width: auto;
}

/* Button Rounded */
.btn--rounded {
